body {
    font-family: 'Inter', sans-serif;
    margin: 0;
}
body {
    overflow-y: hidden;
    overflow-x: hidden;
}

a{
    text-decoration: none !important;
}


/* header styling */

.header-left{
    position: absolute;
    left: 3%;
    top: 0;
}
.header-left h2{
    font-size: 70px;
    font-weight: 600;
}

/* Main styling */

/* Sidebar styling */
.sideBar {
    height: 100%;
    width: 30%;
}

/* Background texts */

.transparent-text{
    position: absolute;
    left: -10px;
    bottom: 600px;
}

.transparent-text-heading{
    font-size: 525px;
    color: rgba(105, 173, 140, 0.4);
}

.tt-span-one{
    position: absolute;
    top: 10px;
    left: -150;
    z-index: -1;
}

.transparent-text-heading-2{
    font-size: 525px;
    color: rgba(192, 91, 84, 0.4);
}

.tt-span-three{
    position: absolute;
    top: 450px;
    left: -150;
    z-index: -1;
}

.transparent-text-heading-3{
    font-size: 525px;
    color: rgba(234, 185, 74, 0.4);
}

.tt-span-four{
    position: absolute;
    top: 450px;
    left: 700px;
    z-index: -1;
}

/* Motto phrase styling */

.motto{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
}

.motto p{
    position: absolute;
    width: 300px !important;
    font-style: italic;
    color: rgba(34, 34, 34, 0.7);
    left: 3.25%;
    top: 17%;
    z-index: 1;
}


/* Background image styling */

.background {
    background-color: white;
    opacity: 0.8;
    position: absolute;
    top: 0;
    width: 30%;
    height: 100%;
    z-index: 0;
}


/* Logo styling */

img{
    max-width: 100%;
    max-height: 100%;
}

.logo {
    position: absolute;
    top: 40%;
    left: 8.5%;
    width: 20%;
    height: 20%;
}


/* Button styling */

.getstartedbtn {
    position: absolute;
    top: 80%;
    left: 3%;
    z-index: 1;
}

.button {
    border-radius: 8px;
    background-color: #f9e3bc;
    border: black;
    color: black;
    text-align: center;
    font-size: 25px;
    padding: 20px;
    width: 300px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
    box-shadow: 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
  }
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }

/* Made by Team Sushi styling */
.credits {
    font-size: 10px;
    z-index: 1;
    color: black;
    position: absolute;
    bottom: 10px;
    left: 110px;
}
.login-signup-box {
  text-align: center;
  width: 320px;
  margin: auto;
  margin-top: 10%;
  border-radius: 10px;
  border: 3px solid #B3F5C4;
  padding: 10px;
  background-color: white;
}

.login-signup-button button{
  height: 30px;
  width: 50%;
  font-size: 15px;
  margin: 15px;
  background-color: #B3F5C4;
  border-radius: 10px;
  border: 1px solid;
  cursor: pointer;
}

.login-signup-button a {
  color: black;
  text-decoration: none;
  margin: 8px;
}

.form {
  text-align: left;
  font-size: 15px;
  display: grid;
  grid-template-rows: auto;
  grid-row-gap: 15px;
  row-gap: 15px;
  margin-top: 10px;
}

.form span {
  margin-bottom: 10px;
}

.form label {
  margin: 10px;
}

.form-fields input { 
  margin-right: 10px;
  float: right;
  width: 50%;
  height: 30px;
  border-radius: 10px;
  border: 2px;
  background-color: #D9D9D9;
}

.form-fields .material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 20;
  display: inline-flex;
  vertical-align: middle;
  margin-right: 5px;
}


.keep-signedin {
  text-align: left;
  margin-left: 15px;
  display: inline-flex;
  vertical-align: middle;
}

.keep-signedin input {
  margin-left: 5px;
}

#submit-button {
  height: 30px;
  width: 30%;
  border-radius: 10px;
  border: 2px solid #B3F5C4;
  margin: auto;
  background-color: #B3F5C4;
  cursor:pointer;
}


/* navigation bar */
.ham-nav {
    overflow: hidden;
    background-color: white;
    position: relative;
  }

/* logo */
.nav-bar-logo {
    width: 40px;
}

/* hamburger icon */
.ham-nav a.ham-icon {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

.ham-icon .material-symbols-outlined {
    font-variation-settings:
        'FILL' 0,
        'wght' 400,
        'GRAD' 0,
        'opsz' 48
}

/* nav links */
.ham-nav a {
    color: black;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
}

/* grey background on hover over links */
.ham-nav a:hover {
    background-color: #ddd;
    color: black;
}



.master-logo-icon,
.menu-icon,
.search-icon {
  position: relative;
  flex-shrink: 0;
  object-fit: cover;
}
.master-logo-icon {
  border-radius: 142px;
  width: 60px;
  height: 65.89px; 
}
.menu-icon,
.search-icon {
  width: 40px;
  height: 39px;
}
.menu-icon {
  width: 39px;
}
.search-and-menu {
  flex-direction: row;
  align-items: flex-start;
  grid-gap: var(--gap-md);
  gap: var(--gap-md);

}
.header-div,
.icon-buttons-div,
.search-and-menu {
  display: flex;
  justify-content: flex-start;
}
.icon-buttons-div {
  flex-direction: row;
  align-items: center;
  grid-gap: 1199px;
  gap: 1199px;
}
.header-div {
  align-self: stretch;
  background-color: #fff;
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  position: relative;
  flex-direction: column;
  padding: 13px 28px;
  align-items: flex-start;
  height: 7vw;
}
.skip-back-icon {
  position: relative;
  width: 45px;
  height: 45px;
  flex-shrink: 0;
  object-fit: cover;
}
.next-shopping-list {
  position: relative;
  display: inline-block;
  width: 450px;
  height: 31px;
  flex-shrink: 0;
}
.next-shopping-list-title-group {
  align-self: stretch;
  font-size: xx-large;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  grid-gap: 8px;
  gap: 8px;
}
.rectangle-div {
  position: relative;
  border-radius: var(--br-md);
  background-color: var(--color-gray-100);
  box-shadow: 0 10px 8px rgba(0, 0, 0, 0.25);
  width: 200px;
  height: 176px;
  flex-shrink: 0;
  border-radius: 10%;
}
.item-name-div {
  position: relative;
  display: inline-block;
}
.demo-items-row-1,
.item-demo-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  grid-gap: var(--gap-sm);
  gap: var(--gap-sm);
}
.demo-items-row-1 {
  flex-direction: row;
  align-items: flex-start;
  grid-column-gap: 1vw;
  -webkit-column-gap: 1vw;
          column-gap: 1vw;
}
.items-row-group {
  grid-gap: 43px;
  gap: 43px;
}
.content-div,
.demo-items-group,
.items-row-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content-div {
  font-size: 2vw;
}
.span {
  color: red;
}
.made-with-by-teamsushi {
  position: relative;
  font-size: 10px;
  display: inline-block;
  width: 208px;
  padding: 1vw;
}
.next-shopping-list-desktop {
  position: relative;
  background-color: #f5f5f2;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: var(--gap-md);
  gap: var(--gap-md);
  text-align: left;
  font-size: 48px;
  color: var(--color-black);
  font-family: var(--font-inter);
}

